<template >
    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 mt-3 Search-panel px-1">
        <div class="heading-border">
            <h5 class="h5">
                <span>{{ $t('ProductMoveToList') }}</span>

            </h5>
        </div>
        <div class="search-content">
            <div id="search-content" class="w-100 mt-2 d-inline-block">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 mb-2">
                    <Form v-slot="{ valid, errors }" ref="addProdcutMoveList">
                        <div class="row mt-2">
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left ">
                                <div class="form-group">

                                    <label>{{ $t('Location') }}<span class="text-danger">*</span></label>

                                    <Field name="Location" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">

                                        <tree-view v-model="TreeValue" :options="options" placeholder="Select Location"
                                            :show-count="true"
                                            v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                        </tree-view>

                                        <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{ errors[0]
                                        }}</span>
                                    </Field>


                                </div>
                            </div>
                        </div>
                        <div class="row mt-8">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left ">
                                <label for="Subject">{{ $t('Description') }}<span class="text-danger">*</span></label>

                                <Field name="description" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                    <textarea name="description" type="text" v-model="txtDescription" class="form-control"
                                        placeholder="Description" :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                        maxlength="500"></textarea>

                                    <span v-if="errors[0]" name="description" class="invalid-feedback d-block">{{ errors[0]
                                    }}</span>
                                </Field>


                            </div>
                        </div>

                        <div class="listing mt-2">
                            <div class="row">
                                <div class="col-12 mb-2">
                                    <div class="mr-2 d-inline-block">
                                        <span class="legend-ovrdue"></span>{{ $t('LocationNotSame') }}
                                    </div>
                                </div>
                            </div>
                            <div class="table-responsive">
                                <table class="table table-bordered mt-0">
                                    <thead class="thead-bg">
                                        <tr>

                                            <th class="text-center">
                                                <div class="custom-control custom-checkbox"
                                                    @click="checkSelectedRecord(item)">
                                                    <input class="chkItems custom-control-input" v-model="selectAll"
                                                        type="checkbox" id="chkAll" name="checkbox"><label
                                                        class="custom-control-label universal-custom-control-label pt-1"></label>
                                                </div>
                                            </th>

                                            <th scope="col" class="text-center"><span>{{ $t('ItemCode') }}</span></th>
                                            <th scope="col" class="text-center"><span>{{ $t('PRODUCT_NAME') }}</span></th>
                                            <th scope="col"><span>{{ $t('UniqueName') }}</span></th>
                                            <th scope="col"><span> {{ $t('Location') }} </span></th>
                                            <th scope="col" class="text-center"><span> {{ $t('NUMBER_OF_ITEM') }}</span>
                                            </th>
                                            <th scope="col" class="text-center"><span>{{ $t('AvailableItems') }}</span></th>
                                            <th scope="col" class="text-center"><span> {{ $t('TransferQuantity') }} </span>
                                            </th>
                                            <th scope="col" class="text-center"> {{ $t('Action') }}</th>
                                        </tr>
                                    </thead>

                                    <tbody v-if="selectItemData.length > 0 ? watchvalue = true : watchvalue = false">
                                        <tr v-for="(item, index) in  selectItemData " :key="index">
                                            <td class="text-center">


                                                <div class="custom-control custom-checkbox"
                                                    v-on:click="singleCheckRecord(item)">
                                                    <input class="chkItems custom-control-input" type="checkbox"
                                                        name="checkbox" style="display: none;"
                                                        v-model="item.isSelectProduct">
                                                    <label
                                                        class="custom-control-label universal-custom-control-label pt-1"></label>
                                                </div>

                                            </td>
                                            <td>{{ item.ItemCode }}</td>
                                            <td>{{ item.AssetCatalogName }}</td>
                                            <td>{{ item.AssetUniqueName }}</td>
                                            <td>{{ item.LocationName }}</td>
                                            <td class="text-center single-action">
                                                <span class="text-dark"><i class="fa fa-list" aria-hidden="true"></i><span
                                                        class="noti-circle noti-blue">{{ item.TotalQuantity }}</span></span>
                                            </td>
                                            <td class="text-center single-action">
                                                <span class="text-dark"><i class="fa fa-list-ul"
                                                        aria-hidden="true"></i><span class="noti-circle noti-green">{{
                                                            item.AvailableItems }}</span></span>
                                            </td>
                                            <td>
                                                <input v-if="item.CreateUniqueNameListing" type="number"
                                                    class="form-control" v-model="item.TransferQuantityNew"
                                                    disabled="disabled">
                                                <input v-else type="number" min="1" v-on:keyup="checkQuantity(item)"
                                                    :class="{ 'form-control': true, 'is-invalid': item.TransferQuantityNew == 0 ? true : false }"
                                                    v-model="item.TransferQuantityNew">
                                            </td>
                                            <td class="text-center"><a href="javascript:;"
                                                    @click="deleteItem(item.AssetCatalogueId)" title="Delete"><i
                                                        class="fa fa-trash text-danger "></i></a></td>
                                        </tr>

                                    </tbody>
                                    <tbody v-else class="NoRecordsFound">
                                        <tr>
                                            <td colspan="15" class="text-center text-middle text-danger no-record">{{
                                                $t('NoRecordfound') }}</td>
                                        </tr>
                                    </tbody>

                                </table>
                            </div>
                        </div>

                        <div class="row mt-8">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left ">
                                <label for="Subject">{{ $t('Comment') }}<span class="text-danger"></span></label>
                                <textarea v-model="txtComment" class="form-control" cols="30" rows="4"
                                    maxlength="2000"></textarea>
                            </div>
                        </div> <br />

                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                            <div class="row flex-row-reverse">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" v-on:click="SubmitData(valid, errors)"
                                        class="btn btn-success formbtn text-white mr-1" :title="$t('Save')"><i
                                            class="fa fa-save pr-2"></i>
                                        <span>{{ $t('Save') }}</span></a>
                                    <a href="javascript:;" @click="onCancel()" :title="$t('Cancel')"
                                        class="btn btn-danger formbtn text-white"><i class="fa fa-close pr-2"></i><span>{{
                                            $t('Cancel') }}</span></a>
                                </div>
                                <div class="col-lg-6">
                                    <div class="py-2">
                                        <small class="text-danger">{{ $t('MandatoryString') }}</small>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Form>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

import DataService from '../../service/DataService';
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'

export default {
    name: "ProductMoveToList",
    components: {
        'tree-view': Treeselect
    },
    props: {

        selectItemData: {
            type: Array,
            required: true,
        },
        Location: {
            type: Number,
        }

    },
    data() {
        return {
            TreeValue: null,
            txtComment: '',
            txtDescription: '',
            singleChecked: false,
            isSingleChecked: false,
            selectedItems: [],
            selectAll: false,
            watchvalue: false,
            selectedCheckItem: [],
            isValidation: false,
            list: [],
            options: []
        }
    },
    created() {
        this.GetLocationByuserId();
    },
    watch: {
        watchvalue(newval) {
            if (newval) {
                var vm = this;
                vm.isSingleChecked = true
            }
        }
    },
    methods: {
        GetLocationByuserId: async function () {
            var vm = this;
            var url = `reqForm=transfer_to_ddl`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        singleCheckRecord: function (item) {
            var vm = this;
            vm.selectAll = true;
            item.isSelectProduct = !item.isSelectProduct;
            if (item.isSelectProduct) {
                vm.searchData.forEach(element => {
                    if (!element.isSelectProduct) {
                        vm.selectAll = false;
                    }
                })
            }
            else {
                vm.selectAll = false;
            }
        },
        checkSelectedRecord: function () {
            var vm = this;
            vm.selectAll = !vm.selectAll
            if (vm.selectAll) {
                vm.selectItemData.forEach(element => {
                    element.isSelectProduct = true;
                })
            } else {
                vm.selectItemData.forEach(element => {
                    element.isSelectProduct = false;
                })
            }
        },
        checkQuantity: function (item) {

            var vm = this;
            let TotalQuantity = item.AvailableItems;
            let currentValue = item.TransferQuantityNew.replace(/[^0-9]/g, "");
            if (parseInt(currentValue) > TotalQuantity) {
                const getRecode = this.selectItemData.find(x => x.AssetCatalogueId === item.AssetCatalogueId)
                getRecode.TransferQuantityNew = '';
                return vm.ShowAlert(
                    vm.$t("Transfer quantity should not be greater than the available quantity."),
                    "warning",
                    vm.$t("Alert")
                );
            }
            else {

                const getRecode = this.selectItemData.find(x => x.AssetCatalogueId === item.AssetCatalogueId)
                getRecode.TransferQuantityNew = currentValue.replace(/[^0-9]/g, "");;
                return vm.isValidation = false;
            }
        },
        SubmitData: function () {
            var vm = this;
            vm.$refs.addProdcutMoveList.validate().then(result => {
                let MoveToInfo = "";
                let totalSelectedQuantity = 0;
                var rid = 0 // required RelatedInfoId
                var qty = 0//dyanmic;
                var description = vm.txtDescription;
                var comment = vm.txtComment;
                vm.isValidation = false;
                if (vm.selectAll == false && vm.isSingleChecked == false) {
                    return vm.ShowAlert(
                        vm.$t("Minimum One Product should be selected."),
                        "warning",
                        vm.$t("Alert")
                    );
                }
                if (vm.TreeValue == '' || vm.txtDescription == '') {
                    return;
                }
                if (vm.TreeValue == this.Location) {
                    return vm.ShowAlert(
                        vm.$t("LocationNotSame"),
                        "warning",
                        vm.$t("Alert")
                    );
                }
                var isChecked = false;
                var isCorrectHighlightedError = false;
                let AssetCatalogueId = this.selectItemData[0].AssetCatalogueId;
                let CreateUniqueNameListing = this.selectItemData[0].CreateUniqueNameListing;
                vm.selectItemData.forEach(ele => {
                    if (ele.isSelectProduct) {
                        isChecked = true;
                        if (ele.TransferQuantityNew == 0) {
                            isCorrectHighlightedError = true;
                        }
                        rid = ele.RelatedInfoId;
                        qty = ele.TransferQuantityNew;

                        MoveToInfo += rid + '*' + qty + '*' + description + '*' + comment + '|';
                        totalSelectedQuantity += qty;
                    }
                });
                if (!isChecked) {
                    return vm.ShowAlert(
                        vm.$t("AtleastSelectOneItem"),
                        "warning",
                        vm.$t("Alert")
                    );
                }
                if (isCorrectHighlightedError) {
                    return vm.ShowAlert(
                        vm.$t("CorrectHighlightedErrors"),
                        "warning",
                        vm.$t("Alert")
                    );
                }
                var formData = new FormData();
                let obj = {
                    AssetCatalogueId: AssetCatalogueId,
                    ItemName: '',
                    ItemGroupName: '',
                    ItemType: '',
                    Quantity: '',
                    RelatedInfoId: '',
                    LocationId: vm.TreeValue,
                    LocationName: '',
                    NumberOfItems: '',
                    UniqueName: '',
                    Description: '',
                    AvailableItems: '',
                    CreateUniqueNameListing: CreateUniqueNameListing,
                    ItemCode: '',
                    MoveToInfo: MoveToInfo,
                    ParentLocationId: ''
                }
                var postString = { "postString": obj }
                formData.append("postString", JSON.stringify(postString));
                DataService.UpdateQuantityOnLocation(formData).then(response => {
                    if (response.data != null) {
                        var result = response.data;
                        if (result.result == "success") {
                            vm.ShowAlert(
                                vm.$t("Product has been successfully Transferred"),
                                `${result.result}`,
                                vm.$t("Alert")
                            );
                            return this.$router.push({ name: "ProductListing", });
                        }
                        else {
                            vm.ShowAlert(
                                vm.$t(`${result.msg}`),
                                `${result.result}`,
                                vm.$t("Alert")
                            );
                        }
                    }
                });
            })
        },
        onCancel: function () {
            this.$router.push({ name: "ProductListing", });
        },
        deleteItem(value) {
            var vm = this;
            vm.confirmR(vm.$t('Are you sure you want to delete these record(s)?'), true, false, function (result) {
                const index = vm.selectItemData.findIndex(x => x.AssetCatalogueId === value);
                if (index !== -1) {
                    vm.selectItemData.splice(index, 1);
                }
                vm.selectAll = false;

                return vm.ShowAlert(
                    vm.$t("Product has been deleted Successfully "),
                    "success",
                    vm.$t("Alert")
                );

            });
        }

    },


}
</script>

<style>
.vue-treeselect__control {
    border: none;
    height: 24px;
    padding: 0;
}

.vue-treeselect--searchable .vue-treeselect__input-container {
    padding: 0 !important;
}

.vue-treeselect__placeholder {
    color: #303030 !important;
}

.vue-treeselect__control-arrow {
    color: #000;
}

.vue-treeselect__placeholder {
    line-height: 24px;
}
</style>
