<template>
    <section>
        <loader :is-visible="isLoading"></loader>

        <template v-if="isVisible == true">

            <div class="listing">
                <div class="table-responsive">
                    <table class="table table-bordered">
                        <thead class="thead-bg">
                            <tr>
                                <th class="text-center">
                                    <div class="custom-control custom-checkbox" @click="checkAllSearchRecord()">
                                        <input class="chkItems custom-control-input" v-model="selectAll" type="checkbox"
                                            id="chkAll" name="checkbox"><label
                                            class="custom-control-label universal-custom-control-label pt-1"></label>
                                    </div>

                                </th>

                                <th scope="col" class="text-center"><span>{{ $t('ItemCode') }}</span></th>
                                <th scope="col" class="text-center"><span>{{ $t('PRODUCT_NAME') }}</span></th>
                                <th scope="col"><span>{{ $t('UniqueName') }}</span></th>
                                <th scope="col"><span> {{ $t('Location') }} </span></th>
                                <th scope="col" class="text-center"><span> {{ $t('NUMBER_OF_ITEM') }}</span></th>
                                <th scope="col" class="text-center"><span>{{ $t('AvailableItems') }}</span></th>
                                <th scope="col" class="text-center"><span> {{ $t('TransferQuantity') }} </span></th>
                            </tr>
                        </thead>

                        <tbody>
                            <tr>
                                <td></td>
                                <td>
                                    <input type="text" v-model="searchItemCode" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Item Code">
                                </td>
                                <td>
                                    <input type="text" v-model="searchItemName" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Item Name">
                                </td>
                                <td>
                                    <input type="text" v-model="searchUniqueName" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Unique Name">
                                </td>

                                <td>
                                    <a href="javascript:;" @click="resetSearchField()"
                                        class="btn btn-danger clearsearchtext clearAll">
                                        <i class="fa fa-refresh text-white"></i>
                                    </a>
                                </td>
                            </tr>
                            <tr v-for="(item,index) in searchData" :key="index">
                                <td class="text-center">
                                    <div class="custom-control custom-checkbox" v-on:click="singleCheckRecord(item)">
                                        <input class="chkItems custom-control-input" type="checkbox" name="checkbox"
                                            style="display: none;" v-model="item.isSelected">
                                        <label class="custom-control-label universal-custom-control-label pt-1"></label>
                                    </div>
                                </td>
                                <td>{{ item.ItemCode }}</td>
                                <td>{{ item.AssetCatalogName }}</td>
                                <td>{{ item.AssetUniqueName }}</td>
                                <td>{{ item.LocationName }}</td>
                                <td class="text-center single-action">
                                    <span class="text-dark"><i class="fa fa-list" aria-hidden="true"></i><span
                                            class="noti-circle noti-blue">{{ item.TotalQuantity }}</span></span>
                                </td>
                                <td class="text-center single-action">
                                    <span class="text-dark"><i class="fa fa-list-ul" aria-hidden="true"></i><span
                                            class="noti-circle noti-green">{{ item.AvailableItems }}</span></span>
                                </td>
                                <td>
                                    <input v-if="item.CreateUniqueNameListing" type="number" class="form-control"
                                        v-model="item.TransferQuantity" disabled="disabled">
                                    <input v-else type="number" min="1" v-on:keyup="checkQuantity(item)"
                                        :class="{ 'form-control': true, 'is-invalid': item.TransferQuantity == 0 ? true : false }"
                                        v-model="item.TransferQuantity">
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <Pager  v-bind:numberofrecords="TotalRecord" :CurrentPageNumber="CurrentPage" prev-text="Prev" first-text="First" last-text="Last" next-text="Next"  v-on:SetCurrentPage="pagerMethod"></Pager>

            </div>

        </template>

        <template v-else>
            <div class="listing">
                <div class="table-responsive">
                    <table class="table table-bordered">
                        <thead class="thead-bg">
                            <tr>
                                <th class="text-center">
                                    <div class="custom-control custom-checkbox" @click="checkAllSearchRecord()">
                                        <input class="chkItems custom-control-input" v-model="selectAll" type="checkbox"
                                            id="chkAll" name="checkbox"><label
                                            class="custom-control-label universal-custom-control-label pt-1"></label>
                                    </div>

                                </th>

                                <th scope="col" class="text-center"><span>{{ $t('ItemCode') }}</span></th>
                                <th scope="col" class="text-center"><span>{{ $t('PRODUCT_NAME') }}</span></th>
                                <th scope="col"><span>{{ $t('UniqueName') }}</span></th>
                                <th scope="col"><span> {{ $t('Location') }} </span></th>
                                <th scope="col" class="text-center"><span> {{ $t('NUMBER_OF_ITEM') }}</span></th>
                                <th scope="col" class="text-center"><span>{{ $t('AvailableItems') }}</span></th>
                                <th scope="col" class="text-center"><span> {{ $t('TransferQuantity') }} </span></th>
                            </tr>
                        </thead>
                        <tbody v-if="filterRecodes.length > 0">
                            <tr>
                                <td></td>
                                <td>
                                    <input type="text" v-model="searchItemCode" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Item Code">
                                </td>
                                <td>
                                    <input type="text" v-model="searchItemName" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Item Name">
                                </td>
                                <td>
                                    <input type="text" v-model="searchUniqueName" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Unique Name">
                                </td>

                                <td>
                                    <a href="javascript:;" @click="resetSearchField()"
                                        class="btn btn-danger clearsearchtext clearAll">
                                        <i class="fa fa-refresh text-white"></i>
                                    </a>
                                </td>
                            </tr>


                            <tr v-for="(item,index) in filterRecodes" :key="index">
                                <td class="text-center">
                                    <div class="custom-control custom-checkbox" v-on:click="singleCheckRecord(item)">
                                        <input class="chkItems custom-control-input" type="checkbox" name="checkbox"
                                            style="display: none;" v-model="item.isSelected">
                                        <label class="custom-control-label universal-custom-control-label pt-1"></label>
                                    </div>
                                </td>
                                <td>{{ item.ItemCode }}</td>
                                <td>{{ item.AssetCatalogName }}</td>
                                <td>{{ item.AssetUniqueName }}</td>
                                <td>{{ item.LocationName }}</td>
                                <td class="text-center single-action">
                                    <span class="text-dark"><i class="fa fa-list" aria-hidden="true"></i><span
                                            class="noti-circle noti-blue">{{ item.TotalQuantity }}</span></span>
                                </td>
                                <td class="text-center single-action">
                                    <span class="text-dark"><i class="fa fa-list-ul" aria-hidden="true"></i><span
                                            class="noti-circle noti-green">{{ item.AvailableItems }}</span></span>
                                </td>
                                <td>
                                    <input v-if="item.CreateUniqueNameListing" type="number" class="form-control"
                                        v-model="item.TransferQuantity" disabled="disabled">
                                    <input v-else type="number" min="1" v-on:keyup="checkQuantity(item)"
                                        :class="{ 'form-control': true, 'is-invalid': item.TransferQuantity == 0 ? true : false }"
                                        v-model="item.TransferQuantity">
                                </td>
                            </tr>
                        </tbody>

                        <tbody v-else>
                            <tr>
                                <td></td>
                                <td>
                                    <input type="text" v-model="searchItemCode" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Item Code">
                                </td>
                                <td>
                                    <input type="text" v-model="searchItemName" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Item Name">
                                </td>
                                <td>
                                    <input type="text" v-model="searchUniqueName" v-on:keyup="searchByColumnName()"
                                        class="form-control" placeholder="Unique Name">
                                </td>

                                <td>
                                    <a href="javascript:;" @click="resetSearchField()"
                                        class="btn btn-danger clearsearchtext clearAll">
                                        <i class="fa fa-refresh text-white"></i>
                                    </a>
                                </td>
                            </tr>
                            <td colspan="12" class="text-danger text-center no-record">No Record
                                Found</td>
                        </tbody>
                    </table>

                </div>
                <Pager  v-bind:numberofrecords="TotalRecords" :CurrentPageNumber="CurrentPage" prev-text="Prev" first-text="First" last-text="Last" next-text="Next"  v-on:SetCurrentPage="pagerMethod"></Pager>
            </div>
        </template>


        <br />
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
            <div class="form-group">
                <button type="submit" @click="AddProductMoveToList" class="btn btn-info float-right"><i
                        class="fa fa-plus mr-2"></i>{{ $t('AddToList')
                        }}</button>
            </div>
        </div>
        <br />
    </section>
</template>
<script>
import Pager from '../Common/Pager.vue'

export default {
    components: { Pager },
    props: {
        searchData: {
            type: Array,
            required: true,
        },      
        TotalRecord: {
            type: Number,
            required: true,
        },     
        CurrentPage: {
            type: Number,
            required: true,
        },     

    },

    watch: {
        searchData: {
            handler(newValue, oldValue) {
                if (newValue) {
                    this.selectAll = false
                }
            },
            deep: true
        }
    },

    created: function () {    
        this.isVisible = true;

    },
    data() {
        return {
            searchUniqueName: '',
            searchItemName: '',
            searchItemCode: '',
            TotalRecords: 0,
            CurrentPage: 1,
            PageSize: 10,
            PageNumber: 1,
            filterRecodes: {},
            isVisible: false,
            selectedCheckItem: [],
            selectAll: false,
            singleChecked: false,
            isSingleChecked: false,
            checkAssetCatalogueId: 0,
            isValidation: false,
            MoveSelectedList: [],
            NoRecordFound: false,
            isLoading: false

        };
    },
   
    methods: {

        pagerMethod: function (value) {
            debugger;           
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;        
                    
            this.$parent.ShowsearchData(value);
                      
        },       
        checkAllSearchRecord: function () {
            var vm = this;
            vm.selectAll = !vm.selectAll
            if (vm.selectAll) {
                vm.searchData.forEach(element => {
                    element.isSelected = true;
                })
            } else {
                vm.searchData.forEach(element => {
                    element.isSelected = false;
                })
            }

        },

        searchByColumnName: function () {
            debugger;
            let vm = this;
            vm.filterRecodes = null;
            if (vm.searchItemCode != "") {
                vm.filterRecodes = vm.searchData.filter(x => x.ItemCode.includes(vm.searchItemCode))
             
                return vm.isVisible = false;
            }
            else if (vm.searchItemName != "") {
                const str2 = vm.searchItemName.charAt(0).toLocaleLowerCase() + vm.searchItemName.slice(1);
                vm.filterRecodes = vm.searchData.filter(x => ((x.AssetCatalogName).toLocaleLowerCase()  .includes(vm.searchItemName.toLocaleLowerCase())))
             
                return vm.isVisible = false;
            }
            else if (vm.searchUniqueName != "") {
                const str2 = vm.searchUniqueName.charAt(0).toLocaleLowerCase() + vm.searchUniqueName.slice(1);
                vm.filterRecodes = vm.searchData.filter(x => (x.AssetUniqueName).toLocaleLowerCase().includes(vm.searchUniqueName.toLocaleLowerCase()))
             
                return vm.isVisible = false;
            }
            else if (vm.searchItemCode == "" || vm.searchItemName == "" || vm.searchUniqueName == "") {
                return vm.isVisible = true;

            }

        },

        checkSelectedRecord: function () {
            var vm = this;
            vm.isSingleChecked = false;
            vm.checkAssetCatalogueId = 0;
            vm.selectedCheckItem = []
            if (vm.selectAll == false) {

                vm.searchData.forEach(function (element) {
                    vm.selectedCheckItem.push(element);
                    vm.singleChecked = true;
                })
                vm.selectAll = true;
            } else {

                vm.searchData.forEach(function (element) {
                    vm.selectedCheckItem = element;
                    vm.singleChecked = false
                })
                vm.selectAll = false
                vm.selectedCheckItem = []

            }

        },

        singleCheckRecord: function (item) {
            var vm = this;
            vm.selectAll = true;
            item.isSelected = !item.isSelected;
            if (item.isSelected) {
                vm.searchData.forEach(element => {
                    if (!element.isSelected) {
                        vm.selectAll = false;
                    }
                })
            }
            else {
                vm.selectAll = false;
            }

        },
        checkQuantity: function (item) {

            var vm = this;
            let TotalQuantity = item.AvailableItems;
            let currentValue = item.TransferQuantity.replace(/[^0-9]/g, "");

            if (parseInt(currentValue) > TotalQuantity) {
                const getRecode = this.searchData.find(x => x.AssetCatalogueId === item.AssetCatalogueId)
                getRecode.TransferQuantity = '';
                return vm.ShowAlert(
                    vm.$t("Transfer quantity should not be greater than the available quantity."),
                    "warning",
                    vm.$t("Alert")

                );

            }
            else {

                const getRecode = this.searchData.find(x => x.AssetCatalogueId === item.AssetCatalogueId)
                getRecode.TransferQuantity = currentValue.replace(/[^0-9]/g, "");
                return vm.isValidation = false;
            }

        },

        resetSearchField: function () {
            let vm = this;
            vm.searchItemCode = ""
            vm.searchItemName = ""
            vm.searchUniqueName = ""
            vm.isVisible = true;

        },

        AddProductMoveToList: function () {
            var vm = this;
            vm.isLoading = true;
            var isChecked = false;
            var isCorrectHighlightedError = false;
            var isAlreadyExists = false;

            vm.searchData.forEach(ele => {
                if (ele.isSelected) {
                    isChecked = true;
                    if (ele.TransferQuantity == 0) {
                        isCorrectHighlightedError = true;
                    }
                    if (vm.MoveSelectedList.length > 0) {
                        if (vm.MoveSelectedList.find(x => x.RelatedInfoId == ele.RelatedInfoId)) {
                            vm.isLoading = false;
                            return vm.ShowAlert(
                                vm.$t("AlreadyExists").replace('{0}', ele.AssetUniqueName),
                                "warning",
                                vm.$t("Alert")
                            );
                        }
                    }

                    vm.MoveSelectedList.push(ele);
                }
            });

            if (!isChecked) {
                vm.MoveSelectedList = [];
                vm.isLoading = false;
                return vm.ShowAlert(
                    vm.$t("AtleastSelectOneItem"),
                    "warning",
                    vm.$t("Alert")
                );
            }

            if (isCorrectHighlightedError) {

                vm.MoveSelectedList = [];
                vm.isLoading = false;

                return vm.ShowAlert(
                    vm.$t("CorrectHighlightedErrors"),
                    "warning",
                    vm.$t("Alert")
                );
            }

            vm.MoveSelectedList.forEach(element => {
                element.isSelectProduct = false;
                element.TransferQuantityNew = element.TransferQuantity;
            })
            vm.selectAll = false
            vm.isLoading = false;

            vm.$emit('ProductMoveToList', vm.MoveSelectedList);

        }

    },
};
</script>

<style scoped>
.listing {
    float: none;
    width: 100%;
    position: relative;
}
</style>
