<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 mt-3 Search-panel px-1" id="divassestsearch">
            <div class="heading-border">
                <h5 class="h5">
                    <span>{{ $t('Search') }}</span>
                </h5>
            </div>
            <div class="search-content mt-2">
                <div id="search-content" class="col-lg-12 float-left  px-0 show">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                        <div class="table-heading-wb leave-details bg-grey border">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left py-1">
                                <Form v-slot="{ valid, errors }" ref="addBarCodeForm">
                                    <div class="row">
                                        <div class="col-12 col-sm-12 col-md-4 col-lg-4 col-xl-4 float-left ">
                                            <div class="form-group pt-3">
                                                <Field name="BarCode" class="w-100" :rules="{ 'required': true }"
                                                    v-slot="{ errors }">
                                                    <input name="BarCode" placeholder="Enter Barcode" v-model="txtBarCode"
                                                        :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                        type="text">
                                                    <span v-if="errors[0]" name="BarCode"
                                                        class="invalid-feedback d-block">{{
                                                            errors[0] }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="search-btm-btn background-0 text-left py-3">
                                                <a v-on:click="SearchByProductBarCode(valid, errors)" href="javascript:;"
                                                    title="Click Here To Search" class="btn btn-success float-left"><i
                                                        class="fa fa-search"></i></a>
                                                <a href="javascript:;" v-on:click="ClearBarcode()" title="Clear"
                                                    class="btn btn-danger float-left mx-1"><i class="fa fa-refresh"></i></a>
                                                <a v-if="RFIDSetupButton" class="pullleft mx-1 green btn btn-info"
                                                    title="Click Here To Search"><i
                                                        class="fa fa-barcode text-white float-left mt-1 mr-2"></i><span
                                                        class="text-white">{{ $t('RFIDScanned') }}</span></a>
                                            </div>
                                        </div>
                                    </div>
                                </Form>

                            </div>
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                        <div class="row mt-2">
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left ">
                                <div class="form-group">
                                    <label>{{ $t('Location') }}<span class="text-danger">*</span></label>
                                    <Field name="Location" class="w-100" :rules="{ 'required': true }" v-slot="{ errors }">
                                        <tree-view v-model="TreeValue" :options="options"
                                            v-on:input="BindItemTypeByUserId(TreeValue)" placeholder="Select Location"
                                            :show-count="true"
                                            v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                        </tree-view>
                                        <span v-if="errors[0]" name="Location" class="invalid-feedback d-block">{{ errors[0]
                                        }}</span>
                                    </Field>
                                </div>
                            </div>
                            <div
                                class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left mt-3 mt-sm-3 mt-md-0 mt-lg-0 mt-xl-0">
                                <div class="form-group">
                                    <label>{{ $t('ProductType') }}<span class="text-danger">*</span></label>
                                    <Field name="Product Type" class="w-100" :rules="{ 'required': true }"
                                        v-slot="{ errors }">
                                        <select name="Product Type" v-model="txtAssetType"
                                            v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }"
                                            @change="GetCompanyAssestCatalogs(txtAssetType)">
                                            <option value="">Select</option>
                                            <option v-for="(item, itemIndex) in AssetTypeData" :key="itemIndex"
                                                :value="item.ASSETID">{{ item.ASSETTYPE }}</option>
                                        </select>
                                        <span v-if="errors[0]" name="Product Type" class="invalid-feedback d-block">{{
                                            errors[0] }}</span>
                                    </Field>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                                <div class="form-group">
                                    <label>{{ $t('ProductName') }}<span class="text-danger">*</span></label>
                                    <Field name="Product Name" class="w-100" :rules="{ 'required': true }"
                                        v-slot="{ errors }">
                                        <select name="Product Name" v-model="txtProduct"
                                            @change="ProductNameChange(txtProduct)"
                                            v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }">
                                            <option value="">Select</option>
                                            <option v-for="(item, itemIndex) in ProducttData" :key="itemIndex"
                                                :value="item.ASSET_CATALOGUE_ID">{{ item.NAME }}</option>
                                        </select>
                                        <span v-if="errors[0]" name="Product Name" class="invalid-feedback d-block">{{
                                            errors[0] }}</span>
                                    </Field>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 float-left">
                                <div class="form-group">
                                    <label> {{ $t('Products') }}</label>
                                    <div class="d-flex" style="align-items: flex-start;">
                                        <Field name="Products" :rules="{ 'required': true }" label="ItemName"
                                            v-slot="{ errors }" style="width:85%">
                                            <div class="input-group mb-3" style="position: relative;">
                                                <input name="Products" id="Products" type="text"
                                                    v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                    v-model="itemname" value="iselectVal" />
                                                <a class="btn float-left" href="javascript:;"
                                                    style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;"
                                                    @click="ItemListOnclick"><em
                                                        class="dropdown-toggle float-right"></em></a>
                                                <span v-if="errors[0]" name="Products" class="invalid-feedback d-block">{{
                                                    errors[0] }}</span>
                                                <div class="unique_dynamicdatalist">
                                                    <ul v-for="(item, index) in ProductsArray"
                                                        @click="setProductsName(item)" style="z-index: auto;" :key="index">
                                                        <li v-if="showProductDropdown" style="text-transform: capitalize;"
                                                            :value="item.Value">{{ item.Name }}</li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </Field>
                                        <div class="input-group mb-3 w-auto" style="position: relative;">
                                            <div class="input-group-append">
                                                <span class="input-group-text  border-0 bg-transparent p-0 pl-2">
                                                    <a id="Searchassest" href="javascript:;" @click="ShowsearchData()"
                                                        class="btn btn-outline-success float-left" title=""
                                                        data-toggle-tooltip="tooltip"
                                                        data-original-title="Click here to search"><i
                                                            class="fa fa-search"></i></a>
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <TransferSearchList :TotalRecord="TotalRecord"  :CurrentPage="CurrentPage" :searchData="searchData"  v-if="IsAssetOfList"   @ProductMoveToList="ProductMoveToList">
                        </TransferSearchList>
                        <ProductMoveToList :selectItemData="selectItemData" :options="options" :Location="TreeValue">
                        </ProductMoveToList>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../service/DataService';
import TransferSearchList from '../Modal/TransferSearchList.vue';
import ProductMoveToList from "../../views/AssetCatalog/ProductMoveToList.vue";
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'

export default {
    components: {
        TransferSearchList,
        ProductMoveToList,
        'tree-view': Treeselect
    },
    data() {
        return {
            TreeValue: null,
            ProducttData: [],
            Groupname: '',
            LocationData: [],
            AssetTypeData: [],
            popover: {
                visibility: 'click'
            },
            txtAssetType: '',
            UserId: null,
            txtProduct: '',
            txtItemName: '',
            itemname: '',
            ProductsArray: [],
            showProductDropdown: false,
            iselectVal: '',
            ShowProducttbl: false,
            GetParentDetails: null,
            containerCls: '',
            IsAssetOfList: false,
            issueTo: null,
            txtBarCode: '',
            FirstfolderShareOption: '',
            Isbarcodediv: false,
            IsEnableAssetManagement: false,
            searchData: [],
            barcodeData: [],
            dropSearchBaseData: [],
            selectItemData: [],
            options: [],
            IsEnableDisableButton: false,
            RFIDSetupButton: true,
            isLoading: false,
            CurrentPage:1,
            TotalRecord:0,

        }
    },
    created: function () {
        this.UserId = this.GetUserInfo.ID;
        this.EnableAssetManagement();
        this.EnableDisableButton();
    },
    methods: {
        async EnableAssetManagement() {
            let url = `moduleName=Asset&configkey=CanSeeAssetManagement`;
            await DataService.EnableAssetManagement(url).then(response => {
                this.IsEnableAssetManagement = response.data;

                if (this.IsEnableAssetManagement) {
                    this.GetLocationByuserId();
                }
            });
        },
        GetLocationByuserId: async function () {
            var vm = this;
            var url = `reqForm=transfer`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        async BindItemTypeByUserId() {
            var vm = this;
            vm.isLoading = true;
            if (vm.TreeValue == null) {
                vm.isLoading = false;

                return;
            } else {
                vm.itemname = null;
                vm.txtProduct = null;
                vm.txtAssetType = null;
                let url = `type=ITEMCODE&issueTo=0&locationId=${vm.TreeValue}&requestType=ASSIGN&requestFrom=transfer`;
                await DataService.AssignGetAssetType(url).then(response => {
                    this.AssetTypeData = response.data;
                    vm.isLoading = false;

                });
                vm.isLoading = false;
            }
        },
        async GetCompanyAssestCatalogs(ths) {
            var vm = this;
            vm.isLoading = true;
            let url = `id=${ths}&issueTo=0&locationId=${this.TreeValue}&requestFrom=`;
            await DataService.GetCompanyAssestCatalogs(url).then(response => {
                this.ProducttData = response.data;
                vm.isLoading = false;
            });
            vm.isLoading = false;

        },
        setProductsName: function (event) {
            var vm = this;
            vm.iselectVal = event.Value;
            vm.itemname = event.Name;
            vm.showProductDropdown = false;
        },

        async ItemListOnclick() {
            var url = `assetCatalogId=${this.txtProduct}&issueTo=&locationId=${this.TreeValue}&requestFrom=transfer&name=&assetType=`;
            await DataService.GetUniqueAssetItems(url).then(response => {
                this.ProductsArray = response.data;
                this.showProductDropdown = true;
            });
        },
        ProductNameChange: function () {
            this.ProductsArray = [];
            this.itemname = '';
        },
        searchfilter: function () {

        },
        OpenAssetOfList: function () {
            this.IsAssetOfList = true;
            this.ShowProducttbl = false;
        },
        SearchByProductBarCode() {
            this.$refs.addBarCodeForm.validate().then(result => {
                if (this.txtBarCode == null || this.txtBarCode == "") {
                    return;
                } else {
                    const url = `id=&isView=1&barcode=${this.txtBarCode}&assetType=&assetUniqueId=&selfCheckout=&reqFrom=transfer&isFor=search&isuniqueSearch=&issueTo=&assignedFrom=&assignedTill=&locationId=&RFID=0&pageSize=&pageNum=1&assignedFromDateTime=&assignedTillDateTime=&startTime=&endTime=&uniqueIds=&catalogueIds=`;
                    DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                        this.barcodeData = [];
                        this.IsAssetOfList = true;
                        if (response.data[0] == null) {
                            return;
                        }

                        this.barcodeData = response.data[0];
                        var temp = [];
                        temp.push(this.barcodeData);
                        temp.forEach(ele => {
                            const GetAlreadyExist = this.searchData.findIndex(x => x.AssetCatalogueId === ele.AssetCatalogueId);
                            if (GetAlreadyExist != -1) {
                                this.searchData.splice(GetAlreadyExist, 1);
                            }
                        })
                        this.searchData.push(this.barcodeData);
                        temp.forEach(ele => {
                            const GetValueofTransferQuantity = this.searchData.find(x => x.AssetCatalogueId === ele.AssetCatalogueId)
                            GetValueofTransferQuantity.TransferQuantity = '' && GetValueofTransferQuantity.isSingleChecked;

                            var availableItems = ele.AvailableItems;
                            ele.isSelected = false;
                            if (availableItems == "1") {
                                ele.TransferQuantity = availableItems
                            } else {
                                ele.TransferQuantity = ''
                            }
                        })
                    });
                }
            })
        },

        async ShowsearchData(event) {          
            let vm = this;
            let url = '';
            vm.isLoading = true;
            if (event == null) {
                url = `id=${vm.txtProduct}&isView=1&barcode=&assetType=${vm.txtAssetType}&assetUniqueId=${vm.iselectVal}&selfCheckout=&reqFrom=transfer&isFor=search&isuniqueSearch=&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${vm.TreeValue}&RFID=0&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;

            }
            else {
                url = `id=${vm.txtProduct}&isView=1&barcode=&assetType=${vm.txtAssetType}&assetUniqueId=${vm.iselectVal}&selfCheckout=&reqFrom=transfer&isFor=search&isuniqueSearch=&issueTo=&assignedFrom=&assignedTill=&&startTime=&endTime=&locationId=${vm.TreeValue}&RFID=0&pageSize=${event.PageSize}&pageNum=${event.PageNumber}&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;

            }
            await DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                vm.searchData = [];
                if (response.data[0] == null) {
                    vm.isLoading = false;
                    return;
                }
                if (response.data.length > 1) {
                    vm.searchData = response.data;
                    vm.isLoading = false;
                    vm.IsAssetOfList = true;
                    vm.searchData.forEach(ele => {
                        var availableItems = ele.AvailableItems;
                        ele.isSelected = false;
                        if (availableItems == "1") {
                            ele.TransferQuantity = availableItems
                        } else {
                            ele.TransferQuantity = ''
                        }
                    })                   
                    vm.TotalRecord = vm.searchData[0].TOTALRECORDS;                  
                    //vm.$children[7].pagerMethods();
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.$children[7].PageSize);
                    vm.CurrentPage = vm.$children[7].PageNumber;
                    return;
                }
                vm.isLoading = false;
                vm.IsAssetOfList = true;
                vm.dropSearchBaseData = response.data[0];
                var temp = [];
                temp.push(vm.dropSearchBaseData);
                temp.forEach(ele => {
                    
                    const GetAlreadyExist = this.searchData.findIndex(x => x.AssetCatalogueId === ele.AssetCatalogueId);
                    if (GetAlreadyExist != -1) {
                        this.searchData.splice(GetAlreadyExist, 1);
                    }
                })
                vm.searchData.push(this.dropSearchBaseData);
                temp.forEach(ele => {
                   
                    const GetValueofTransferQuantity = this.searchData.find(x => x.AssetCatalogueId === ele.AssetCatalogueId)
                    GetValueofTransferQuantity.TransferQuantity = '' && GetValueofTransferQuantity.isSingleChecked;
                    var availableItems = ele.AvailableItems;
                    ele.isSelected = false;
                    if (availableItems == "1") {
                        ele.TransferQuantity = availableItems
                    } else {
                        ele.TransferQuantity = ''
                    }              
                })
                
                vm.TotalRecord = vm.searchData[0].TOTALRECORDS;
                // vm.$children[7].pagerMethods();
                vm.TotalPages = Math.ceil(vm.TotalRecord / vm.$children[7].PageSize);
                vm.CurrentPage = vm.$children[7].PageNumber;
                        
            });
            vm.isLoading = false;
        },
        ClearBarcode: function () {
            return this.txtBarCode = " ";
        },
        ProductMoveToList: function (ProductMoveToList) {
            var vm = this;
            vm.selectItemData = ProductMoveToList;
            console.log(vm.selectItemData);
        },
        EnableDisableButton() {
            var vueObj = this;
            vueObj.isLoading = true;
            DataService.GetEnableDisableGroupCode().then(response => {
                vueObj.isLoading = false;
                if (response.data.length > 0) {
                    if (response.data[0].IS_ENABLE == 0) {
                        this.RFIDSetupButton = false;
                    } else {
                        this.RFIDSetupButton = true;
                    }
                } else {
                    this.RFIDSetupButton = true;
                }
            })
            vueObj.isLoading = false;

        }
    }
}
</script>

<style>
.vue-treeselect__control {
    border: none;
    height: 24px;
    padding: 0;
}

.vue-treeselect--searchable .vue-treeselect__input-container {
    padding: 0 !important;
}

.vue-treeselect__placeholder {
    color: #303030 !important;
}

.vue-treeselect__control-arrow {
    color: #000;
}

.vue-treeselect__placeholder,
.vue-treeselect__single-value {
    line-height: 24px;
}

.vue-treeselect__placeholder.vue-treeselect-helper-zoom-effect-off {
    padding: 0;
}
</style>
